define('bridgetowncomedy/helpers/schedule-table-header-row', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.scheduleTableHeaderRow = scheduleTableHeaderRow;

  window.FESTIVAL_START_TIMES = {
    Thursday: "2015-05-07T01:30:00.000Z",
    Friday: "2015-05-08T01:00:00.000Z",
    Saturday: "2015-05-09T20:00:00.000Z",
    Sunday: "2015-05-10T20:00:00.000Z"
  };

  window.FESTIVAL_END_TIMES = {
    Thursday: "2015-05-07T08:30:00.000Z",
    Friday: "2015-05-08T10:00:00.000Z",
    Saturday: "2015-05-09T10:00:00.000Z",
    Sunday: "2015-05-10T08:00:00.000Z"
  };

  function getTimeArray(day) {

    var timeArray = [];
    var duration = moment(window.FESTIVAL_END_TIMES[day]).diff(window.FESTIVAL_START_TIMES[day], "hours");
    for (var i = 0; i < duration * 2; i++) {
      timeArray.push(moment(window.FESTIVAL_START_TIMES[day]).add(i * 30, "minutes"));
    }
    return timeArray;
  }
  function scheduleTableHeaderRow(day) {
    var html = "<th>Venue</th>";
    var timeArray = getTimeArray(day);
    Ember['default'].$.each(timeArray, function (index, time) {
      var $el = Ember['default'].$("<tr><th class=\"schedule-table__time-header\" data-start-time=\"" + time.toISOString() + "\">" + time.format("MM/DD ") + "<br />" + time.format("h:mm a") + "</th></tr>");
      html += $el.html();
    });
    return new Ember['default'].Handlebars.SafeString(html);
  }

  exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(scheduleTableHeaderRow);

});