define('bridgetowncomedy/initializers/route-mods', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.initialize = initialize;

  function initialize() {}

  exports['default'] = {
    name: "route-mods",
    initialize: function initialize() {
      Ember['default'].Route.reopen({
        render: function render() {
          this._super();
          window.scrollTo(0, 0);
        },
        activate: function activate() {
          this._super();
          if (this.get("title")) {
            document.title = this.get("title") + " | Bridgetown Comedy Festival";
          } else {
            document.title = "Bridgetown Comedy Festival";
          }
        },
        deactivate: function deactivate() {
          if (Ember['default'].$(".navbar .navbar-collapse.collapse.in").length) {
            Ember['default'].$(".navbar-toggle").not(".collapsed").click();
          }
        },
        loadAllData: function loadAllData() {
          return Ember['default'].RSVP.hash({
            venues: this.store.find("venue"),
            events: this.store.find("event"),
            performers: this.store.find("performer"),
            shows: this.store.find("show")
          });
        },

        _getLocalHtml: function _getLocalHtml(pageUrl) {
          var host = "http://www.bridgetowncomedy.com";
          if (window.location.host === "localhost:4000") {
            host = "http://localhost:4000";
          }
          return Ember['default'].$.ajax({
            url: host + pageUrl,
            dataType: "html"
          }).then(function (data) {
            return Ember['default'].$(data).find("#jekyll-content").html();
          });
        }
      });
    }
  };
  /* container, application */
  // application.inject('route', 'foo', 'service:foo');

});