define('bridgetowncomedy/helpers/google-map-link', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.googleMapLink = googleMapLink;

  function googleMapLink(address /*, hash*/) {
    var html = "Address: <a target='blank' href='http://maps.google.com/?q=" + address + "'>" + address + " </a>";
    return new Ember['default'].Handlebars.SafeString(html);
  }

  exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(googleMapLink);

});