define('bridgetowncomedy/initializers/trackerrors', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.initialize = initialize;

  function initialize() {}

  exports['default'] = {
    name: "trackerrors",
    initialize: function initialize() {
      // Log Emberjs errors to TrackJS

      Ember['default'].onerror = function (error) {
        window.ga("send", "exception", {
          exDescription: error.stack,
          exFatal: false
        });

        // Optional pass error through to embers error logger
        Ember['default'].Logger.assert(false, error);
      };

      // Log Ember promise errors
      Ember['default'].RSVP.on("error", function (error) {

        window.ga("send", "exception", {
          exDescription: error.stack,
          exFatal: false
        });

        // Optional pass error through to embers error logger
        Ember['default'].Logger.assert(false, error);
      });
    }
  };
  /* container, application */
  // application.inject('route', 'foo', 'service:foo');

});