define('bridgetowncomedy/helpers/get-weekday', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.getWeekday = getWeekday;

  function getWeekday(dateString) {
    return moment(dateString.toString()).utcOffset("-07:00").format("dddd");
  }

  exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(getWeekday);

});