define('bridgetowncomedy/controllers/schedule', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].ObjectController.extend({
    searchField: "",
    actions: {
      filterDay: function filterDay(weekday) {
        this.set("filterWeekday", weekday);
      },
      filterGrid: function filterGrid() {
        Ember['default'].$(".schedule-table").show();
        Ember['default'].$(".schedule-list").hide();
      },
      filterList: function filterList() {
        Ember['default'].$(".schedule-table").hide();
        Ember['default'].$(".schedule-list").show();
      }
    },

    isShowingThursday: Ember['default'].computed.equal("filterWeekday", "thursday"),
    isShowingFriday: Ember['default'].computed.equal("filterWeekday", "friday"),
    isShowingSaturday: Ember['default'].computed.equal("filterWeekday", "saturday"),
    isShowingSunday: Ember['default'].computed.equal("filterWeekday", "sunday"),

    filterWeekday: null,

    isNotFiltered: (function () {
      return !this.get("filterWeekday");
    }).property("filterWeekday"),

    events: (function () {
      var searchRegex = new RegExp(this.searchField, "i");
      var filteredWeekday = this.get("filterWeekday");
      return Ember['default'].ArrayProxy.createWithMixins(Ember['default'].SortableMixin, {
        sortProperties: ["start_time"],
        content: this.get("content.events").filter(function (event) {
          return !filteredWeekday || event.get("weekday") === filteredWeekday;
        }).filter(function (event) {

          // Search through event name for a match
          if (event.get("Name").match(searchRegex)) {
            return true;
          }

          // Search through event Description for a match
          if (event.get("Description").match(searchRegex)) {
            return true;
          }

          // Search through venue name for a match
          if (event.get("venue.Name").match(searchRegex)) {
            return true;
          }

          // Search through performers for a match
          if (event.get("performers").filter(function (p) {
            return p.get("Name").match(searchRegex);
          }).length) {
            return true;
          }

          return false;
        })
      });
    }).property("content.events", "filterWeekday", "searchField")
  });

});