define('bridgetowncomedy/views/schedule_grid', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  window.FESTIVAL_START_TIMES = {
    Thursday: "2015-05-07T01:30:00.000Z",
    Friday: "2015-05-08T01:00:00.000Z",
    Saturday: "2015-05-09T20:00:00.000Z",
    Sunday: "2015-05-10T20:00:00.000Z"
  };

  window.FESTIVAL_END_TIMES = {
    Thursday: "2015-05-07T08:30:00.000Z",
    Friday: "2015-05-08T10:00:00.000Z",
    Saturday: "2015-05-09T10:00:00.000Z",
    Sunday: "2015-05-10T08:00:00.000Z"
  };

  exports['default'] = Ember['default'].View.extend({
    scheduleDay: "Thursday",
    addTableColspans: (function () {
      var scheduleDay = this.scheduleDay;
      Ember['default'].$(".cell-spacer").remove();
      Ember['default'].$(".schedule-table__cell").each(function () {
        var startTime = Ember['default'].$(this).data("start-time");
        var endTime = Ember['default'].$(this).data("end-time");
        var duration = moment(endTime).diff(moment(startTime), "minutes") / 30;
        Ember['default'].$(this).attr("colspan", duration);

        var previousTime = Ember['default'].$(this).prevAll(".schedule-table__cell:eq(0)").data("end-time") || window.FESTIVAL_START_TIMES[scheduleDay];
        var durationBefore = moment(startTime).diff(moment(previousTime), "minutes") / 30;
        if (durationBefore <= 0) {
          return;
        }
        Ember['default'].$(this).before("<td class=\"cell-spacer\" colspan=\"" + durationBefore + "\" style=\"border:1px solid white;\"></td>");
      });
    }).on("didInsertElement")
  });

});