define('bridgetowncomedy/helpers/get-time', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.getTime = getTime;

  function getTime(dateString /*, hash*/) {
    return moment(dateString.toString()).utcOffset(-7).format("h:mm a");
  }

  exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(getTime);

});