define('bridgetowncomedy/app', ['exports', 'ember', 'ember-data', 'ember/resolver', 'ember/load-initializers', 'bridgetowncomedy/config/environment'], function (exports, Ember, DS, Resolver, loadInitializers, config) {

  'use strict';

  Ember['default'].MODEL_FACTORY_INJECTIONS = true;

  window.FESTIVAL_START_TIMES = {
    Thursday: "2015-05-07T01:30:00.000Z",
    Friday: "2015-05-08T01:00:00.000Z",
    Saturday: "2015-05-09T20:00:00.000Z",
    Sunday: "2015-05-10T20:00:00.000Z"
  };

  window.FESTIVAL_END_TIMES = {
    Thursday: "2015-05-07T08:30:00.000Z",
    Friday: "2015-05-08T10:00:00.000Z",
    Saturday: "2015-05-09T10:00:00.000Z",
    Sunday: "2015-05-10T08:00:00.000Z"
  };

  var App = Ember['default'].Application.extend({
    modulePrefix: config['default'].modulePrefix,
    podModulePrefix: config['default'].podModulePrefix,
    Resolver: Resolver['default']
  });

  App.FixtureAdapter = DS['default'].FixtureAdapter.extend({
    queryFixtures: function queryFixtures(records, query) {
      return records.filter(function (record) {
        for (var key in query) {
          if (!query.hasOwnProperty(key)) {
            continue;
          }
          var value = query[key];
          if (record[key] !== value) {
            return false;
          }
        }
        return true;
      });
    }
  });

  App.ApplicationStore = DS['default'].Store.extend({
    revision: 11,
    //adapter: DS.RESTAdapter.create()
    //adapter: DS.LSAdapter.create()
    adapter: "Fixture"
  });

  loadInitializers['default'](App, config['default'].modulePrefix);

  exports['default'] = App;

});