define('bridgetowncomedy/helpers/nice-date', ['exports', 'ember'], function (exports, Ember) {

   'use strict';

   exports.niceDate = niceDate;

   function niceDate(dateString /*, hash*/) {
      return moment(dateString.toString()).utcOffset(-7).format("dddd, MMMM Do YYYY");
   }

   exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(niceDate);

});